.areachart {
  padding: 20px;
  margin: 7px;
}

.chart__wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.areachart__header {
  margin: auto;
  width: 250px;
  justify-content: space-between;
  align-items: flex-start;
}

.areachart__header section {
  justify-content: flex-start;
  align-items: flex-start;
}

.areachart__header section > img {
  width: 27px;
  height: 27px;
  border-radius: 50%;
  padding: 7px;
}

.areachart__header span > img {
  width: 0.8em;
}

.areachart__header section p {
  font-weight: 600;
  font-size: 0.75em;
  display: inline-block;
  margin-right: 10px;
}

.areachart__header div {
  background: #fff2;
  padding: 4px 14px;
  padding-right: 10px;
  border-radius: 10px;
}

.expences__chart {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.expences__section {
    display: grid;
    grid-template-columns: 2fr 1fr;
}

.expences__enteries {
    margin: 7px;
    font-weight: 500;
}

.expences__enteries--header {
    margin: 0 25px;
    margin-top: 25px;
    align-items: flex-start;
    justify-content: space-between;
}

.expences__enteries--header div {
    padding: 10px;
    padding-left: 15px;
    background: #fff1;
}

.expences__enteries .MuiSvgIcon-root {
    color: #84818A !important;
    width: 20px !important;
    margin-left: 5px;
}

.expences__enteries--header span {
    color: #fff8;
}

.expences__enteries--title {
    margin: 20px 0;
    background: #fff1;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 1.8fr 1.3fr 1fr 1fr;
    align-items: center;
    justify-content: space-around;
    padding: 10px 15px;
}

.expences__enteries--content {
    margin: 0 25px;
    margin-bottom: 25px;
}

.expences__enteries--header>p {
    font-size: 1.1em;
}

.expences__enteries--title .MuiSvgIcon-root {
    margin-bottom: -4px;
}

.expences__enteries--title span:nth-child(1) {
    text-align: left;
}

.expences__enteries--title span {
    text-align: center;
}

.expences__enteries--title span:nth-child(4) {
    text-align: right !important;
}

.expences__enteries--row {
    display: grid;
    grid-template-columns: 1.9fr 1fr 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    margin-bottom: 5px;
}

.expences__enteries--row>section {
    justify-content: flex-start;
    text-align: center;
}

.expences__enteries--row section section {
    text-align: left;
}

.expences__enteries--row span {
    width: 7px;
    height: 6px;
    border-radius: 50%;
    margin-right: 15px;
    margin-top: -17px;
}

.expences__enteries--row p {
    font-size: .8em;
    color: #84818A;
}

.expences__enteries--row>section:nth-child(2) {
    max-width: 100px;
    text-align: center;
    padding: 3px;
    border-radius: 7px;
}

.expences__donutchart {
    margin: 7px;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
}

.expences__chart--label {
    font-size: .85em;
    font-weight: 500;
}

.expences__chart--label main span {
    width: 7px;
    height: 6px;
    border-radius: 50%;
    margin-right: 15px;
}

.expences__chart--label span {
    color: #fff8;
}

.expences__chart--label {
    margin-top: 20px;
}

.expences__chart--label section {
    width: 200px;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.revenue {
  display: flex;
  width: 99%;
}

.revenue__left {
  flex: 0.6 1;
  padding-right: 20px;
}

.revenue__header {
  margin: 7px;
  padding: 10px 20px;
  justify-content: space-around;
}

.revenue__header img {
  background: #0bafff30;
  padding: 10px;
  border-radius: 50%;
  margin-right: 20px;
}

.revenue__barchart {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.revenue__right {
  padding-top: 25px;
  margin: -25px;
  margin-left: 0;
  flex: 4 1;
  background: #35373d;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.revenue__right h3 {
  width: 100%;
  padding: 50px;
  padding-bottom: 0;
}

.revenue__right > section {
  border-top: 0.5px solid #fff1;
  padding: 25px 0;
  width: 100%;
}

.revenue__right--content {
  font-size: 0.7em;
  justify-content: space-between;
  padding: 0 50px;
}

.revenue__right--content h5 {
  font-size: 1em;
}

.revenue__right--content img {
  background: #fff2;
  border-radius: 50%;
  padding: 10px;
  margin-right: 10px;
  width: 38px;
  height: 38px;
}

.revenue__right--content > p {
  color: #4fbf67;
}

.revenue__footer {
  width: 90%;
  background: #fff1;
  padding: 6px;
  margin: 10px;
  font-size: 0.8em;
  font-weight: 500;
}

.report__header {
  display: grid;
  grid-template-columns: 50% 50%;
  justify-items: center;
}

.report__header--right {
  justify-content: center;
  width: 94%;
}

.report__header--left {
  width: 100%;
  justify-content: space-between;
}

.report__header--left > div {
  padding: 29px;
  box-sizing: border-box;
  min-width: 250px;
}

.report__header--left section {
  width: 170px;
}

.report__card {
  margin: 10px 0;
  justify-content: space-evenly;
  padding: 0 5px;
}

.reportCard__content {
  padding: 10px;
  flex: 1 1;
  align-items: flex-start;
}
.reportCard__content:first-child {
  margin-right: 30px;
}

.reportCard__content section {
  justify-content: space-between;
  min-width: 0;
}

.report__card > img {
  margin: "0 30px";
}

.report__card {
  width: 100%;
  display: flex;
  justify-content: center;
}

.report__chart {
  justify-content: space-between;
}

.report__header--left > div:nth-child(2) {
  margin-right: 10px;
}

.report__content--left {
  margin-right: 25px;
  flex: 0.5 1;
  flex-direction: column;
}

.report__content--right {
  flex: 0.5 1;
  margin-right: 5px;
  margin-left: -10px;
  flex-direction: column;
}

.report__content {
  display: grid;
  grid-template-columns: 51% 48.5%;
  justify-items: center;
  margin: 20px 0;
  box-sizing: border-box;
}

.report__content > div {
  width: 97%;
  box-sizing: border-box;
}

.report__content section {
  justify-content: space-between;
  padding: 20px 35px;
  width: 100%;
  box-sizing: border-box;
}

.report__content section > span {
  flex-direction: column;
  align-items: flex-start;
}

.report__content section > span:nth-child(2) {
  align-items: flex-end;
}

.report__content section > span span {
  margin-top: 5px;
}

.report__content img {
  margin-right: 7px;
}

.report > section {
  justify-content: space-between;
  padding: 20px;
  font-size: 0.9em;
  margin-right: 20px;
}

.report > section p {
  color: #84818a;
}

.report .left p {
  margin-top: 5px;
}

.report > section div {
  flex: 0.5 1;
  justify-content: flex-start;
}

.report .left > img {
  margin-right: 10px;
}

.report .left p img {
  margin-right: 7px;
  width: 15px;
}

.report .right section {
  justify-content: space-between;
  flex: 0.5 1;
  margin: 5px 20px;
}

.report .right {
  display: flex;
  flex-wrap: wrap;
}

.report .right img {
  margin-left: 10px;
}

.report__footer {
  display: flex;
}

.report__goal {
  flex: 0.65 1;
  margin-top: 20px;
  margin-right: 5px;
}

.report__goal > div {
  padding: 20px 30px;
  justify-content: space-between;
}

.report__goal p {
  color: #84818a;
}

.report__goal div span {
  flex-direction: column;
}

.report__goal div .left {
  align-items: flex-start;
}

.report__goal div .right {
  align-items: flex-end;
}

.report__goal--content > section {
  border: 0.5px solid #fff3;
  border-radius: 15px;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;
  margin-right: 10px;
  width: 230px;
}

.report__goal--content section img {
  margin-right: 10px;
}

.report__goal--content section h1 {
  font-size: 2.5em;
  font-weight: 800;
}

.report__goal--content section > div {
  justify-content: space-between;
  width: 100%;
}

.progress__bar {
  background: #fff;
  width: 70%;
  height: 2px;
  border-radius: 99px;
  position: relative;
}

.progress__bar > span {
  height: 2px;
  border-radius: 99px;
  position: absolute;
}

.report__summary {
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 15px;
  width: 100%;
}

.report__summary h4 {
  padding: 25px 20px;
  border-bottom: 0.5px solid #fff3;
}

.report__summary div {
  border-bottom: 0.5px solid #fff3;
  width: 100%;
  justify-content: space-between;
  padding: 20px;
  font-size: 0.85em;
}

.report__summary div img {
  margin-right: 5px;
  margin-top: 5px;
}

.report__summary section {
  background: #fff2;
  padding: 10px;
  margin: 15px 20px;
  border-radius: 99px;
  font-size: 0.8em;
}

