.auth .MuiCheckbox-root {
    padding: 0;
    padding-right: 5px;
}

.auth .MuiCheckbox-root .MuiSvgIcon-root {
    color: #0BAFFF !important;
}

.auth input[type="checkbox"] {
    width: 20px;
}

.auth__right .MuiButton-root {
    background-color: #0BAFFF !important;
    text-transform: none;
    width: 100%;
    margin: 20px 0;
}

.auth__divider {
    width: 100%;
    position: relative;
    color: #84818A;
}

.auth__divider span {
    position: absolute;
    width: 45%;
    height: 1px;
    background-color: #84818A;
}

.auth__divider span:nth-child(1) {
    left: 0;
}

.auth__divider span:nth-child(2) {
    right: 0;
}

.auth__button {
    width: 100%;
    flex-direction: column;
    margin-top: 15px;
}

.auth__button button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border-radius: 3px;
    border: .5px solid #84818A;
    padding: 7px 20px;
    margin: 10px 0;
    color: #fff;
    font-weight: 400;
    font-size: 1em;
    width: 100%;
}

.auth__button button img {
    margin: 0 10px;
}

@media only screen and (max-width: 500px) {
    .auth__left {
        display: none;
    }

    .auth__right form {
        width: 80vw;
        height: 95vh;
        min-height: 700px;
    }

    .auth>span {
        top: 10px;
        left: 10px;
        z-index: 0;

    }

    .auth>span>img {
        width: 75px;
    }

    .auth__signIn .MuiButton-root {
        padding: 7px 30px;
        width: auto !important;
        min-width: 100px !important;
    }
}
